.section {
  display: flex;

  align-items: center;
  border-bottom-width: 2px;
  padding: 15px;
  min-width: 200px;
  cursor: pointer;
  color: gray;
  border-width: 0 !important;
}
.section--selected {
  background-color: whitesmoke;
  border-width: 3px !important;
}
.section:hover {
  background-color: whitesmoke;
  border-width: 3px !important;
}
.section > h4 {
  font-size: 14px;
  margin-left: 15px;
}
@media only screen and (max-width: 600px) {
  .section {
    display: flex;
    align-items: center;
    border-bottom-width: 2px;
    padding: 15px;
    min-width: 50px;
    cursor: pointer;
    color: gray;
    border-width: 0 !important;
  }
  .section--selected {
    background-color: whitesmoke;
    border-width: 3px !important;
  }
  .section:hover {
    background-color: whitesmoke;
    border-width: 3px !important;
  }
  .section > h4 {
    display: none;
    margin-left: 15px;
  }
}
