.emailList {
  flex: 1;
  overflow: scroll;
}

/* Hide scrollBar for Chrome,Safari and Opera */
.emailList::-webkit-scrollbar {
  display: none;
}
/* Hide scrollbar for IE,Edge and Firefox */
.emailList {
  -ms-overflow-style: none; /* Ie,Edgs*/
  scrollbar-width: none; /*Firefox */
}
.emailList__settings {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid whitesmoke;
  z-index: 999;
  padding-right: 10px;
  background-color: white;
}
.emailList__sections {
  background-color: white;
  position: sticky;
  top: 0;
  display: flex;
  border-bottom: 1px solid whitesmoke;
  z-index: 999;
}
.emailList__list {
  padding-bottom: 20%;
}
@media only screen and (max-width: 600px) {
  .emailList__settings {
    display: none;
  }
}
