.emailRow {
  display: flex;
  align-items: center;
  height: 50px;
  border-bottom: 1px solid whitesmoke;
  cursor: pointer;
  z-index: 999;
}
.emailRow:hover {
  border-top: 1px solid whitesmoke;
  box-shadow: 0px 4px 4px -2px rgba(0, 0, 0, 0.24);
}
.emailRow__options {
  display: flex;
}
.emailRow__message {
  display: flex;
  flex: 0.8;
  align-items: center;
  font-size: 13px;
}
.emailRow__message > h4 {
  width: 400px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-left: 5px;
  padding-right: 5px;
}
.emailRow__title {
  font-size: 13px;
  flex: 0.3;
}
.emailRow__time {
  padding-right: 15px;
  font-size: 9px;
  font-weight: bold;
}
.emailRow__description {
  font-weight: 400;
  color: gray;
}

@media only screen and (max-width: 600px) {
  .emailRow {
    height: 40px;
  }
  .emailRow:hover {
    border-top: 1px solid whitesmoke;
    box-shadow: 0px 4px 4px -2px rgba(0, 0, 0, 0.24);
  }
  .emailRow__options {
    display: none;
    padding-right: 0.5px;
  }
  .emailRow__message {
    display: flex;
    flex: 0.8;
    align-items: center;
    font-size: 9px;
  }
  .emailRow__message > h4 {
    width: 200px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-left: 3px;
    padding-right: 3px;
  }
  .emailRow__title {
    font-size: 12px;
    flex: 0.3;
  }
  .emailRow__time {
    display: none;
  }
  .emailRow__description {
    font-weight: 400;
    color: gray;
  }
}
